:root{

  
    --color1:  #13293D ;
    --color2:  #133c64 ;
    --color3:  #006494 ;
    --color4:  #247Ba0 ;
    --color5:  #bfc1c2 ;
    --color6:  #E8f1f2;
    
    scroll-behavior: smooth;
}

.paginacontato{
    padding: 0;
    
}

#paginaContato{
    min-height: 100vh;
    background-color: #13293D;
    padding-top: 7rem;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 200px rgba(0,0,0,0.9) inset;
}
.titulocontato{
    color: var(--color6);
    text-align: center;
    padding: 2rem;
}
