:root{

  
--color1:  #13293D ;
--color2:  #133c64 ;
--color3:  #006494 ;
--color4:  #247Ba0 ;
--color5:  #bfc1c2 ;
--color6:  #E8f1f2;

scroll-behavior: smooth;

@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@1,500&display=swap');
}
.header{
  margin-top:-1.3rem;
  position:fixed;
  display: flex;
  background-color: var(--color1);
  align-items: center;
  justify-content: space-between;
  padding-left: 3rem;
  padding-right: 3rem;
  width: 100%;
  z-index: 20;
}

.Logo {
  width: 20rem;
}

.menu{
  display: flex;
  margin-left: -3rem;
  padding-right: 7rem;

}

.menu li{
  list-style: none;
  margin-left: 1rem;
  
}

.menu li a{
  text-decoration: none;
  color: var(--color5);
  font-family: 'Nunito', sans-serif;
  text-transform: uppercase;
}

a:hover{
  border-bottom: 3px var(--color6);
}

@media(max-width: 800px) {
  header{
  flex-direction: column;
align-items:flex-end;

  }
  
}