:root{

  
    --color1:  #13293D ;
    --color2:  #133c64 ;
    --color3:  #006494 ;
    --color4:  #247Ba0 ;
    --color5:  #bfc1c2 ;
    --color6:  #E8f1f2;
}    
.sobre{ 
    display: flex;
    align-items: center;
    padding: 5rem;
    padding-top: 20rem;
    width: 60%;
    margin: auto;
    
}

#sobre{
    background-image: url(bg.jpg);
    background-size: cover;
    min-height: 100vh;
}


.perfil{
    width: 20rem;
    border: solid #e8f1f2;
    box-shadow: #13293d 3px 3px 3px;
 
}
.text{
    text-align: justify;
    background-color: var(--color1);
    color: #e8f1f2;
    padding: 3rem
}
.titulo{
    margin-top: 50rem;
}


@media(max-width: 800px) {
    main{
    flex-direction: column;
    background-color: var(--color5) ;

    }
    .sobre{
      padding: auto;
      background-image: url(bg.jpg);
      background-size: cover;
    
    }

    .perfil {
        width: 15rem;
    }

    .text{
        width: 15rem;
        margin-top: 3rem;
    }

    .subtitleabout{
        text-align: center;
    }
  }
